
.ss-solver-layout {
  display: grid;
  gap: 10px;
  grid-template-columns: 50px 200px auto 250px;
  grid-template-rows: auto;
  grid-template-areas: 
    ". header  header . "
    ". puzzle  puzzle . "
    ". actions select . "
}

.ss-layout-heading {
  grid-area: header;
  text-align: center;
}

.ss-select-sudoku {
  grid-area: select;
}

.ss-select-sudoku div {
  display: flexbox
}

